const apikey = '868TLNpZS8ooK19OYaEFOMlNAj6HNSqy'; //–∫–ª—é—á –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏ –¥–ª—è –±–µ–∫–µ–Ω–¥–∞
const BASE_URL = 'https://app.ticketmaster.com/discovery/v2';

class EventService {
  constructor() {
    this.searchQuery = ''; //–ø–æ–∏—Å–∫ –ø–æ –∫–ª—é—á–µ–≤–æ–º—É —Å–ª–æ–≤—É. –ü—Ä–æ–≤–µ—Ä—è–ª –Ω–∞ –∏–º–µ–Ω–∞—Ö –∏—Å–ø–æ–ª–Ω–∏—Ç–µ–ª–µ–π: Bob, Martim, Iglesias
    this.country = null; // –∫–ª—é—á –¥–ª—è –ø–æ–∏—Å–∫–∞ –ø–æ —Å—Ç—Ä–∞–Ω–∞–º.
    this.page = 0; // –Ω–æ–º–µ—Ä —Å—Ç—Ä–∞–Ω–∏—Ü—ã (–¥–ª—è –ø–∞–≥–∏–Ω–∞—Ü–∏–∏)
    // this.eventsOnOnePage = 100; // —á–∏—Å–ª–æ –∫–∞—Ä—Ç–æ—á–µ–∫ –æ —Å–æ–±—ã—Ç–∏—è—Ö –Ω–∞ –æ–¥–Ω–æ–π —Å—Ç—Ä–∞–Ω–∏—Ü–µ. –í–ù–ò–ú–ê–ù–ò–ï –¥–ª—è —ç–∫—Ä–∞–Ω–∞ —Ä–∞–∑–º–µ—Ä–∞ Tablet –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å 21 –∫–∞—Ä—Ç–æ—á–∫–∞ —Å–æ–±—ã—Ç–∏–π
    this.eventsOnOnePage = 19; // —á–∏—Å–ª–æ –∫–∞—Ä—Ç–æ—á–µ–∫ –æ —Å–æ–±—ã—Ç–∏—è—Ö –Ω–∞ –æ–¥–Ω–æ–π —Å—Ç—Ä–∞–Ω–∏—Ü–µ. –í–ù–ò–ú–ê–ù–ò–ï –¥–ª—è —ç–∫—Ä–∞–Ω–∞ —Ä–∞–∑–º–µ—Ä–∞ Tablet –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å 21 –∫–∞—Ä—Ç–æ—á–∫–∞ —Å–æ–±—ã—Ç–∏–π
    this.eventID = ''; // –ø–µ—Ä–µ–º–µ–Ω–Ω–∞—è —Å ID —Å–æ–±—ã—Ç–∏—è/–∫–æ–Ω—Ü–µ—Ä—Ç–∞ (–≤–æ–∑–º–æ–∂–Ω–æ —Å–æ–≤—Å–µ–º –Ω–µ –ø–æ–Ω–∞–¥–æ–±–∏—Ç—Å—è –ø—Ä–∏ —Ä–∞–±–æ—Ç–µ —Å –¥–∞–Ω–Ω—ã–º –∫–ª–∞—Å—Å–æ–º)
    this.numberOfEvens = 0; // —á–∏—Å–ª–æ —Å–æ–±—ã—Ç–∏–π/–∫–æ–Ω—Ü–µ—Ä—Ç–æ–≤, –∫–æ—Ç–æ—Ä—ñ–µ –≤–µ—Ä–Ω—É–ª–∏—Å—å –æ—Ç –±–µ–∫–µ–Ω–¥–∞
  }

  //–≠—Ç–∞ —Ñ—É–Ω–∫—Ü–∏—è –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –ø—Ä–æ–º–∏—Å —Å –º–∞—Å—Å–∏–≤–æ–º –¥–∞–Ω–Ω—ã—Ö (—Å–æ–±—ã—Ç–∏—è/–∫–æ–Ω—Ü–µ—Ä—Ç—ã)
  fetchEvents() {
    if (this.country === null) {
      return fetch(
        `${BASE_URL}/events.json?size=${this.eventsOnOnePage}&keyword=${this.searchQuery}&page=${this.page}&apikey=${apikey}`,
      )
        .then(response => response.json())
        .then(data => {
          this.numberOfEvens = data.page.totalElements;
          if (!data.hasOwnProperty('_embedded')) {
            return info('No events found with the specified search word!');
          }
          return data;
        })

        .catch(error => {});
    } else {
      return fetch(
        `${BASE_URL}/events.json?size=${this.eventsOnOnePage}&keyword=${this.searchQuery}&countryCode=${this.country}&page=${this.page}&apikey=${apikey}`,
      )
        .then(response => response.json())
        .then(data => {
          this.numberOfEvens = data.page.totalElements;
          if (!data.hasOwnProperty('_embedded')) {
            return info('No events with the given search word were found in this country!');
          }
          return data;
        })
        .catch(error => {});
    }
  }

  //–§—É–Ω–∫—Ü–∏—è –¥–ª—è –ø–æ–ª—É—á–µ–Ω–∏—è –ø—Ä–æ–º–∏—Å–∞ —Å –æ–¥–Ω–∏–º –æ–±—ä–µ–∫—Ç–æ–º —Å–æ–±—ã—Ç–∏—è/–∫–æ–Ω—Ü–µ—Ä—Ç–∞ –ø–æ –µ–≥–æ ID
  fetchEventById(searchId) {
    this.eventID = searchId;
    return fetch(`${BASE_URL}/events.json?&id=${this.eventID}&apikey=${apikey}`)
      .then(response => response.json())
      .then(data => {
        return data._embedded.events;
      })
      .catch(error => console.log(error));
  }

  // –§—É–Ω–∫—Ü–∏—è –¥–ª—è –ø–µ—Ä–≤–æ–π –∑–∞–≥—Ä—É–∑–∫–∏ –∫–∞—Ä—Ç–∏–Ω–æ–∫, –¥–æ —Ç–æ–≥–æ –∫–∞–∫ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –≤–≤–æ–¥–∏–ª –∑–∞–ø—Ä–æ—Å—ã.
  // –ò—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–æ API —Å –∫–ª—é—á—ë–º preferredCountry:  Popularity boost by country, default is us. (–ü–æ–≤—ã—à–µ–Ω–∏–µ –ø–æ–ø—É–ª—è—Ä–Ω–æ—Å—Ç–∏ –ø–æ —Å—Ç—Ä–∞–Ω–∞–º, –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é –º—ã.)
  // –≤–æ–∑–º–æ–∂–Ω—ã –∑–Ω–∞—á–µ–Ω–∏—è —Ç–æ–ª—å–∫–æ 2 —Å—Ç—Ä–∞–Ω: String enum:["us", " ca"]
  fetchEventsFirstLoad() {
    return fetch(
      `${BASE_URL}/events.json?size=${this.eventsOnOnePage}&preferredCountry=${('us', 'ca')}&page=${
        this.page
      }&apikey=${apikey}`,
    )
      .then(response => response.json())
      .then(data => {
        return data._embedded.events;
      })
      .catch(error => console.log(error));
  }

  incrementPage() {
    this.page += 1;
  }

  decrementPage() {
    this.page -= 1;
  }

  resetPage() {
    this.page = 0;
  }

  get query() {
    return this.searchQuery;
  }

  set query(newQuery) {
    this.searchQuery = newQuery;
  }

  get —ÅountryQueryKey() {
    return this.country;
  }

  set —ÅountryQueryKey(newCountry) {
    this.country = newCountry;
  }

  get numberOfPage() {
    return this.page;
  }

  set numberOfPage(newPage) {
    this.page = newPage;
  }

  get numberOfEventsOnOnePage() {
    return this.eventsOnOnePage;
  }

  set numberOfEventsOnOnePage(newValue) {
    this.eventsOnOnePage = newValue;
  }

  get EventID() {
    return this.eventID;
  }

  set EventID(newID) {
    this.eventID = newID;
  }
}

export default EventService;
